body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	background-image: url(img/background.png);
	background-repeat: no-repeat;
	background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.wrapper{
  display: flex;
  flex-wrap: wrap;
}

.entryForm{
  position: fixed;
  top: 30%;
  left: 41%;
  display: flex;
  flex-wrap: wrap;
  padding: 2%;
  width: 15%;
  border: 1px solid black;
  background-color: gray;
}

.entryForm input{
  padding: 3%;
  margin-bottom: 3%;
  width: 100%;
}

.entryForm button{
  padding: 3%;
  margin-bottom: 3%;
  width: 100%;
}

.entryForm span{
  width: 100%;
  margin-bottom: 3%;
  text-align: center;
}

#gameOption{
  display: flex;
  justify-content: center;
  margin-bottom: 5%;
}

#root p{
  font-size: 2vw;
  font-weight: bold;
  text-align: center;
}

#gameOption .selectChoose{
  display: flex;
  flex-wrap: wrap;
  margin-right: 5%;
  width: 10%;
}

#gameOption .selectChoose select{
  width: 100%;
  height: 21%;
  margin-bottom: 3%;
}

#gameOption .selectChoose img{
  width: 100px;
  height: 150px;
  border: none;
}

#gameOption button{
  width: 10%;
  height: 21%;
  margin-top: 3%;
  padding: 1%;
}

#gameRules{
  display: flex;
  flex-wrap: wrap;
}

#gameRules .title{
  width: 100%;
  margin-bottom: 5%;
  font-size: 2vw;
  font-weight: bold;
  text-align: center;
}

#gameRules .rules li{
  font-size: 1vw;
  margin-bottom: 5%;
}

#gameField{
  display: flex;
  flex-wrap: wrap;
  margin-top: 3%;
  margin-left: 7%;
  width: 80%;
  border: 1px solid black;
  padding: 3%;
  background: url(img/gameFieldBackground.png) no-repeat;
  background-size: cover;
}

#gameField img{
  height: 150px;
  width:  100px;
  margin-bottom: 1%;
  transition-property: transform;
  transition-duration: 0.3s;
}

#stopwatch{
  margin-left: 46%;
  font-size: 40pt;
  font-weight: bold;
}

#leaderboard{
  margin-left: 10%;
  width: 80%;
  text-align: center;
  border-collapse: collapse;
}

#leaderboard tr{
  border: solid 1px black;
}

#leaderboard td{
  width: 20%;
  border-right: solid 1px black;
}

#menu{
  margin-top: 2%;
  margin-left: 45%;
  width: 10%;
}